<template>
  <div class="admin_login">
    <div class="login-layout">
      <div class="login-w438">
        <h2 class="tit2">全民试驾</h2>
        <div class="login-form-input">
          <i class="ico_user"></i>
          <input type="text" v-model="userName" maxlength="11" placeholder="请输入账号">
        </div>
        <div class="login-form-input">
          <i class="ico_password"></i>
          <input type="password" v-model="passWord" placeholder="请输入密码">
        </div>
        <div class="login-form-input">
          <div class="login_button" @click.prevent="login">登录</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {goLogin} from '@/api/http'
  export default{
    data(){
      return {
        userName: '',
        passWord: '',
      }
    },
    created(){
    },
    methods: {
      login(){
        const data = {
        user_name : this.userName,
        password : this.passWord 
      }
      goLogin(data)
        .then((res)=>{
          console.log(res)
          this.$store.dispatch("isLogin", 1)
          this.$router.push({name:"index"})
        }).catch((err)=>{
          console.log(err)
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .admin_login{
    width: 100%;
    height: 100vh;
    background: #064c79;
    padding-top: 40px;
    .login-layout{
      background:-webkit-gradient(linear,0 0,0 100%,from(#43c9e7),to(#1879ac));
      width: 80%;
      height: 400px;
      border-radius: 8px;
      padding: 20px;
      margin-left: 10%;
      .login-w438{
        .tit2{
          font-size:28px;
          color:#ffffff;
          text-align: center;
          margin-bottom: 10px;
          letter-spacing: 5px;
        }
        .login_error{
          height: 28px;
          line-height: 28px;
          background:#ffe3e3 url('images/login_error.png') 11px 8px no-repeat;
          font-size:12px;
          color:#d03434;
          padding-left: 30px;
          margin-bottom: 10px;
        }
        .login-form-input{
          margin-bottom: 30px;
          position: relative;
          line-height: 0px;
          input[type='text'],input[type='password']{
            width: 100%;
            height: 42px;
            padding: 8px 10px 8px 35px;
            border-radius:4px;
            border: none;
            font-size: 18px;
          }
          i{
            position: absolute;
            width: 14px;
            height: 16px;
            display: block;
            left: 10px;
            top: 13px;
            &.ico_user{
              background: url("images/user.png") 0 0 no-repeat;
            }
            &.ico_password{
              background: url("images/password.png") 0 0 no-repeat;
            }
          }
          .login_button{
            background-color:#064c79;
            border-radius:2px;
            height:46px;
            line-height: 46px;
            display: block;
            font-size:22px;
            letter-spacing: 10px;
            color:#fdfdfd;
            text-align:center;
            &:hover{
              background-color: #095585;
            }
          }
        }
      }
    }
  }
</style>



